.main {
  min-height: 100vh;
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 48px;
  position: sticky;
  top: 0;
  background: var(--bg);
  z-index: 100;
}

.wordmark {
  font-weight: 600;
  font-size: 18px;
  letter-spacing: -0.02em;
}

.links {
  display: flex;
  gap: 32px;
}

.links a {
  font-size: 15px;
  color: var(--muted);
  transition: color 0.15s;
}

.links a:hover {
  color: var(--fg);
}

.cta {
  font-size: 14px;
  font-weight: 500;
  padding: 10px 20px;
  background: var(--fg);
  color: var(--bg);
  border-radius: 8px;
  transition: opacity 0.15s;
}

.cta:hover {
  opacity: 0.85;
}

.hero {
  background: var(--bg-warm);
  border-radius: 24px;
  margin: 0 24px;
  padding: 120px 48px;
  text-align: center;
}

.heroInner {
  max-width: 720px;
  margin: 0 auto;
}

.hero h1 {
  font-size: 56px;
  font-weight: 600;
  letter-spacing: -0.03em;
  line-height: 1.1;
  margin-bottom: 24px;
}

.hero > p,
.heroInner > p {
  font-size: 18px;
  color: var(--muted);
  line-height: 1.6;
  margin-bottom: 40px;
}

.buttons {
  display: flex;
  gap: 12px;
  justify-content: center;
}

.primary {
  font-size: 15px;
  font-weight: 500;
  padding: 14px 28px;
  background: var(--accent);
  color: var(--fg);
  border-radius: 8px;
  transition: background 0.15s;
}

.primary:hover {
  background: var(--accent-hover);
}

.secondary {
  font-size: 15px;
  font-weight: 500;
  padding: 14px 28px;
  background: var(--bg);
  color: var(--fg);
  border-radius: 8px;
  border: 1px solid var(--border);
  transition: border-color 0.15s;
}

.secondary:hover {
  border-color: var(--muted);
}

.features {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 24px;
  padding: 80px 48px;
  max-width: 1200px;
  margin: 0 auto;
}

.feature {
  padding: 32px;
  background: var(--bg);
  border: 1px solid var(--border);
  border-radius: 16px;
}

.icon {
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--bg-warm);
  border-radius: 12px;
  margin-bottom: 20px;
  color: var(--fg);
}

.feature h3 {
  font-size: 17px;
  font-weight: 600;
  margin-bottom: 8px;
}

.feature p {
  font-size: 15px;
  color: var(--muted);
  line-height: 1.5;
}

.docs {
  background: var(--bg-warm);
  border-radius: 24px;
  margin: 0 24px;
  padding: 80px 48px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 48px;
  align-items: center;
}

.docsHeader {
  max-width: 400px;
}

.badge {
  display: inline-block;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: var(--muted);
  background: var(--bg);
  padding: 6px 12px;
  border-radius: 6px;
  margin-bottom: 16px;
}

.docs h2 {
  font-size: 36px;
  font-weight: 600;
  letter-spacing: -0.02em;
  margin-bottom: 12px;
}

.docsHeader > p {
  font-size: 16px;
  color: var(--muted);
}

.code {
  background: var(--fg);
  color: #e8e8e8;
  padding: 32px;
  border-radius: 16px;
  font-family: var(--font-geist-mono), monospace;
  font-size: 14px;
  line-height: 1.7;
  overflow-x: auto;
}

.steps {
  padding: 80px 48px;
  max-width: 900px;
  margin: 0 auto;
  text-align: center;
}

.steps h2 {
  font-size: 36px;
  font-weight: 600;
  letter-spacing: -0.02em;
  margin-bottom: 48px;
}

.stepsGrid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 32px;
  text-align: left;
}

.step {
  padding: 24px;
}

.num {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  background: var(--accent);
  border-radius: 50%;
  font-size: 14px;
  font-weight: 600;
  margin-bottom: 16px;
}

.step h4 {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 8px;
}

.step p {
  font-size: 14px;
  color: var(--muted);
  line-height: 1.5;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 32px 48px;
  border-top: 1px solid var(--border);
  margin-top: 40px;
}

.footerLinks {
  display: flex;
  gap: 24px;
}

.footerLinks a {
  font-size: 14px;
  color: var(--muted);
  transition: color 0.15s;
}

.footerLinks a:hover {
  color: var(--fg);
}

@media (max-width: 768px) {
  .nav {
    padding: 16px 20px;
  }

  .links {
    display: none;
  }

  .hero {
    margin: 0 16px;
    padding: 60px 24px;
    border-radius: 16px;
  }

  .hero h1 {
    font-size: 32px;
  }

  .buttons {
    flex-direction: column;
  }

  .features {
    grid-template-columns: 1fr;
    padding: 48px 20px;
  }

  .docs {
    grid-template-columns: 1fr;
    margin: 0 16px;
    padding: 48px 24px;
    border-radius: 16px;
  }

  .stepsGrid {
    grid-template-columns: 1fr;
  }

  .steps {
    padding: 48px 20px;
  }

  .footer {
    padding: 24px 20px;
    flex-direction: column;
    gap: 16px;
  }
}
